import axios from 'axios';
import { Notify } from 'notiflix/build/notiflix-notify-aio';
import refs from './refs';

const API_KEY = '?api_key=fb4eca5dd3545235e4fd6796c70d4d40';
const MAIN_URL = 'https://api.themoviedb.org/3/';

export default class MoviesApiService {
  constructor() {
    this.searchQuery = '';
    this.page = 1;
    this.language = 'ru';
  }

  async getMovies() {
    try {
      const response = await axios.get(
        `${MAIN_URL}movie/popular${API_KEY}&language=${this.language}&page=${this.page}`
      );
      return response;
    } catch (error) {
      console.error(error);
    }
  }

  async getSearchMovies(query) {
    try {
      const response = await axios.get(
        `${MAIN_URL}search/movie${API_KEY}&language=${this.language}&page=${this.page}&query=${query}`
      );
      if (response.data.results.length < 1) {
        Notify.warning(
          ',    .    '
        );
      } else {
        Notify.success(` ${response.data.total_results} `);
        return response;
      }
    } catch (error) {
      console.error(error);
    }
  }

  cleanHTML() {
    return refs.main.innerHTML = '';
  }

  nextPage() {
    this.page += 1;
  }

  backPage() {
    if (this.page > 1) {
      this.page -= 1;
    }

    return
  }

  getPage() {
    return this.page;
  }

  setPage(currentPage) {
    this.page = currentPage;
  }

  resetPage() {
    this.page = 1;
  }

  changeLanguage(lang) {
    this.language = lang;
  }

  get query() {
    return this.searchQuery;
  }

  set query(newQuery) {
    this.searchQuery = newQuery;
  }

  get pageNum() {
    return this.page;
  }
}
